* {
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
  font-size: 15px;
}

body, h1, h2, p, ul {
  margin: 0;
}

ul {
  padding-left: 0;
  padding-right: 0;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

button {
  font-weight: 600;
}

.container-body {
  width: 100%;
}

.nav-menu {
  display: flex;
}

.nav-menu-list {
  z-index: 1;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
  position: fixed;
}

.nav-menu-items {
  color: #fff;
  text-shadow: 1px 4px 10px #2f312b;
  border: 3px solid #fff;
  padding: 15px 30px;
  font-size: 1.1em;
  box-shadow: 5px 5px 10px #45483f48;
}

.nav-menu-items:hover {
  transform: scale(.95);
}

header {
  background: url("background-header.5cf06ab3.jpg") 0 0 / cover;
  height: 100vh;
}

.menu-container {
  margin: 200px 0 30px 200px;
}

.menu-container-text {
  color: #fff;
  width: 70%;
}

.header-title {
  margin-bottom: 20px;
  font-size: 3.5em;
}

.header-text-extended, .header-text-sumary, .header-text-very-sumary {
  text-align: justify;
  margin-bottom: 60px;
  font-size: 1.1em;
  line-height: 3;
}

.header-text-sumary, .header-text-very-sumary {
  display: none;
}

.header-button {
  color: #fff;
  background: #fff0;
  border: 3px solid #fff;
  padding: 15px 30px;
  font-size: 1.1em;
}

.header-button:hover {
  transform: scale(.95);
}

.container-contents {
  background: url("scene-haikei.02c08361.png") 0 0 / cover;
  width: 100%;
  height: 100vh;
  overflow: scroll;
}

.container-content {
  justify-content: space-evenly;
  margin: 200px 60px;
  display: flex;
}

.containter-content-text {
  background-color: #d4dbc3;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  width: 40%;
  min-width: 30%;
  height: 600px;
  padding: 0 50px 30px;
  display: flex;
  overflow: auto;
  box-shadow: 2px 2px 30px #6e7361;
}

.containter-content-text-title {
  overflow: none;
  text-align: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 0 30px;
  font-size: 2.2em;
}

.containter-content-text-paragraph {
  text-align: justify;
  font-size: 1.1em;
  line-height: 2.7;
  overflow: auto;
}

.container-content-image {
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  height: 100%;
  display: flex;
}

.container-content-image:hover {
  transition: all .3s;
  transform: scale(1.1);
}

.container-content-image img {
  border-radius: 10px;
  box-shadow: 2px 2px 30px #6e7361;
}

.content-horizontal-all {
  background: #b0c8bc;
  width: 100%;
  min-height: 100vh;
}

.content-horizontal {
  white-space: nowrap;
  overflow: auto;
}

.content-horizontal-card {
  background-color: #fff;
  border-radius: 30px;
  justify-content: space-evenly;
  width: 30%;
  min-width: 30%;
  height: auto;
  margin: 140px 100px;
  padding: 10px;
  display: inline-block;
  box-shadow: 8px 8px 50px #6e7361;
}

.content-horizontal-card:hover {
  transition: all .6s;
  transform: scale(1.1);
}

.content-horizontal-title {
  text-align: center;
  margin: 30px 0;
  font-size: 2.2em;
}

.content-horizontal-image {
  position: block;
  border-radius: 20px;
  max-width: 80%;
  height: auto;
  margin: 0 auto;
  box-shadow: 1px 4px 15px #6e7361;
}

.content-horizontal-text {
  white-space: wrap;
  text-align: justify;
  max-width: 80%;
  min-height: 200px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 0 30px;
  font-size: 1em;
  font-weight: 500;
  line-height: 2;
  overflow: scroll;
}

footer {
  background: #333;
  width: 100%;
  height: 35vh;
  font-size: 1.3em;
}

.footer-content {
  color: #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 400px;
  display: flex;
}

.footer-information, .footer-menu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 150px;
  display: flex;
}

.footer-information {
  line-height: 2;
}

.footer-information-links a {
  color: #9bc9b1;
  text-decoration: none;
}

.footer-information-links a:hover {
  font-weight: 700;
}

.footer-menu-list {
  grid-template-rows: 4fr;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px 20px;
  display: grid;
}

.footer-menu-list li:hover {
  color: #9bc9b1;
}

@media (width <= 1000px) {
  .menu-container {
    margin: 200px 0 30px 70px;
  }

  .menu-container-text {
    text-align: justify;
    width: 80%;
  }

  .header-text-extended, .header-text-very-sumary {
    display: none;
  }

  .header-text-sumary {
    display: inline;
  }

  .header-button {
    margin-top: 40px;
  }

  .container-contents {
    background: url("scene-haikei.02c08361.png") 0 0 / cover;
    width: 100%;
    height: 100vh;
  }

  .container-content {
    grid-template-rows: 1fr;
    gap: 8px;
    margin: 130px 60px;
    display: grid;
  }

  .containter-content-text {
    overflow: none;
    border-radius: 20px;
    width: 100%;
    padding: 20px 25px 30px 30px;
  }

  .containter-content-text-title {
    width: 80%;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
    font-size: 2.5em;
  }

  .containter-content-text-paragraph {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.1em;
    line-height: 2.7;
  }

  .container-content-image {
    display: none;
  }

  .content-horizontal-all {
    background: #b0c8bc;
    width: 100%;
    height: 100vh;
  }

  .content-horizontal-card {
    width: 80%;
    margin: 150px 100px 0 90px;
  }

  .content-horizontal-image {
    width: 100%;
  }

  .footer-content {
    gap: 20px;
  }

  .footer-information, .footer-menu {
    margin-top: 100px;
  }

  .footer-information {
    text-align: center;
    line-height: 1.5;
  }

  .footer-menu-list {
    grid-template-rows: 6fr;
    grid-template-columns: repeat(2, 1fr);
    gap: 2%;
  }
}

@media (width <= 400px) {
  .nav-menu-list {
    margin-top: 25px;
  }

  .nav-menu-items, .header-button {
    padding: 7px 15px;
    font-size: .6em;
  }

  .menu-container {
    margin: 100px 0 30px 70px;
  }

  .menu-container-text {
    width: 80%;
  }

  header {
    width: 100%;
    height: 110vh;
  }

  .header-text-extended, .header-text-sumary {
    display: none;
  }

  .header-text-very-sumary {
    display: inline;
  }

  .containter-content-text-title {
    text-align: center;
    margin-bottom: 15px;
    font-size: 1.5em;
  }

  .containter-content-text-paragraph {
    text-align: start;
    width: 80%;
    font-size: .9em;
    line-height: 2;
  }

  .content-horizontal-card {
    width: 80%;
    height: 40em;
    margin: 120px 100px 35px 40px;
  }

  .content-horizontal-card:hover {
    transition: all .6s;
    transform: scale(1.1);
  }

  .content-horizontal-title {
    margin: 20px 0;
    font-size: 1.3em;
    font-weight: 500;
  }

  .content-horizontal-image {
    border-radius: 15px;
    width: 14em;
  }

  .content-horizontal-text {
    width: 13em;
    margin-left: auto;
    margin-right: auto;
    padding: 20px 0;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5;
  }

  footer {
    height: 40vh;
    font-size: 1em;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-information {
    margin-top: 40px;
  }

  .footer-menu {
    margin-top: 20px;
  }

  .footer-menu-list {
    grid-template-rows: 7fr;
    grid-template-columns: repeat(2, 1fr);
    gap: 3%;
    margin-left: 30px;
    display: grid;
  }
}

